import {request} from "../../request.js";



export default {
	data() {
		return {
			url:'',
		}
	},
	onLoad(options) {
		console.log(options);
		if(options.type&&options.type==1){
			this.url = '/pages/mine/mine';
		}
	},
	methods: {
		getWechatPay(){
			request({
				url:"index.php?s=/wap/pay/getPaySessionInfo",
			}).then(res=>{
				var tmpp = JSON.parse(res.data.pay);
				wechat.wchatapply(tmpp,res => {
					if(url) {
						uni.switchTab({
							url: this.url
						})
					}

				});
			})
		}
	}
}
